    -ms-flex-direction: column;
    flex-direction: column;
    background-color: #fff;
    width: 300px;
    -webkit-box-align: start;
    -ms-flex-align: start;
    align-items: flex-start;
    -webkit-transition: right .3s ease-in;
    -o-transition: right .3s ease-in;
    transition: right .3s ease-in;
  }

  header.header nav.navigation a.navigation__link {
    margin: 10px 0;
  }

  header.header nav.navigation a.navigation__link:first-child {
    margin-top: 0;
  }

  header.header nav.navigation a.navigation__link:last-child {
    margin-bottom: 0;
  }

  header.header nav.navigation a.dwnl-btn,
  header.header nav.navigation a.navigation__logo {
    display: none;
  }

  header.header a.header__dwnl {
    position: relative;
    right: 0;
  }

  header.header--active nav.navigation {
    right: 0;
  }
}

@media screen and (max-width:768px) {
  header.header {
    padding: 20px 0;
  }
}

@media screen and (max-width:425px) {
  header.header .header__dwnl {
